/* COLORS */
$white:#FFFFFF;
$black:#000000;
$grey:#adadad;
$lightGrey:#dddddd;
$primaryOrange:#eab96a;
$secondaryOrange:#cdb47b;
$customBlue:#485260;
$checkColor:#6a9c00;
$noColor:#c20000;

/* FONT SIZE */

$baseFontSize:16px;
$linkFontSize:12.5px;
$phoneFontSize:27.5px;
$title:58px;

/* PADDINGS AND MARGINS */
$paddingNavLinks:12px;


/* FONT FAMILY */
$intro:'Intro';
$introRust:'IntroRustG';
$minion:'Minion';
$playFair:'PlayfairDisplay';

@font-face {
    font-family: "Intro";
    src: url('../customfonts/Intro.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "IntroRustG";
    src: url('../customfonts/IntroRustG-Base2Line.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "Minion";
    src: url('../customfonts/MinionPro-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "PlayfairDisplay";
    src: url('../customfonts/PlayfairDisplay-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@mixin flexbox() {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
}

@mixin flexJustContent($justContent){
    justify-content: $justContent;
}

@mixin flexDirection($direction){
    flex-direction: $direction;
}

@mixin flexAlignSelf($alignSelf){
    align-self: $alignSelf;
}
  
@mixin flex($values) {
    -webkit-box-flex: $values;
    -moz-box-flex:  $values;
    -webkit-flex:  $values;
    -ms-flex:  $values;
    flex:  $values;
}
  
  @mixin order($val) {
    -webkit-box-ordinal-group: $val;  
    -moz-box-ordinal-group: $val;     
    -ms-flex-order: $val;     
    -webkit-order: $val;  
    order: $val;
}

@mixin backgroundAlignment(){
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

@mixin imagesSeparator {
    display: block;
    position: absolute;
    z-index: 20;
    width: 100%;
}

[class*="--wrapper"] {
    margin: 0 auto;
    width: 100%;
    max-width: 1194px;
    height: 100%;
}

a{
    text-decoration: none;
}

body{
    font-family: $minion, Arial, sans-serif;
    font-size: $baseFontSize;
    color:$customBlue;
    background-color:$black;
    width: 100%;
}

header{
    height: 100px;
    width: 100%;
    padding-top: 23px;
    color:$white;
    font-family:$intro;
    .logo{
        position: absolute;
        width: 100%;
        height: 1px;
        z-index: 10;
        img{
            width: 273px;
            height: 151px;
            display: inline-block;
        }
        &::before,
        &::after{
        content:"";
        height:1px;
        background-color:white;
        display: inline-block;
        }

        &::before{
            width: 32px;
            margin-right: 21px;
        }
        &::after{
            width: calc( 100% - (45px + 313px));
            margin-left: 16px;
        }
    }
    .header__navigation{
        @include flexbox();
        @include flexJustContent(center);
        z-index: 20;
        .header__navigation__region{
            height: 89px;
            @include flexbox();
            @include flexJustContent(center);
            @include flexDirection(column);
            .nav{
                @include flexAlignSelf(flex-end);
                z-index: 30;
                ul{
                    li{
                        float: left;
                        .nav__item{
                            font-size: $linkFontSize;
                            color:$white;
                            float: left;
                            padding-left: $paddingNavLinks;
                            padding-right: $paddingNavLinks;
                            padding-bottom: 37px;
                            border-bottom: 4px solid transparent;
                            &:hover,
                            &:active,
                            &:focus{
                                border-bottom: 4px solid $primaryOrange;
                            }
                        }
                        &::after{
                            content:"â€¢";
                            color:$white;
                            width: 10px;
                            height: 10px;
                            padding-left: 30px;
                            padding-right: 30px;
                        }
    
                        &:last-child::after{
                            display: none;
                        }
                    }
                }
            }
        }
        .header__navigation__phone{
            font-size: $phoneFontSize;
            padding-right: 43px;;
            padding-bottom: 30px;
            z-index: 20;
            @include flexAlignSelf(center);
            a{
                color:$white;
            }
        }
    }
    
}

main{
    width: 100%;

    .top-section{
        height: 708px;
        position: relative;
        background-image: url("../images/top-bg.jpg");
        @include backgroundAlignment;
        margin-top: -192px;
        padding-top: 192px;
        color:$white;
        text-align: center;
        .top-section__image-separator{
            @include imagesSeparator;
            bottom: -27px;
            img{
                width: 100%;
                height: 100px;
            }   
        }
        .top-section__content__stars{
            color:$primaryOrange;
            margin-bottom: 33px;
            i{
                margin-right: 24px;
                &:last-child{
                    margin-right: 0;
                }
            }
        }
        .top-section__content__pre-title{
            font-family: $playFair;
            margin-bottom: 57px;
            &::before,
            &::after{
                content:"";
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 3px 102px 3px 0;
                border-color: transparent #cdb47b transparent transparent;
                display: inline-block;
            }
            &::before{
                margin-right: 82px;
            }
            &::after{
                margin-left: 82px;               
                transform:rotate(180deg);
            }
        }
        .top-section__content__title{
            margin-bottom: 61px;
            h1{
            font-size: $title;
            font-family: $introRust;
                span{
                    color:$primaryOrange
                }
            }
        }
        .top-section__content__subtitle{
            font-family: $playFair;
        }
        .top-section__content__decorative{
            color: $primaryOrange;
            &::before,
            &::after{
                content:"";
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 3px 102px 3px 0;
                border-color: transparent #cdb47b transparent transparent;
                display: inline-block;
            }
            &::before{
                margin-right: 82px;
            }
            &::after{
                margin-left: 82px;               
                transform:rotate(180deg);
            }
        }
        .decorative-bean{
            position: absolute;
            right: 100px;
            bottom: -100px;
            z-index: 30;
        }
    }
}